import React, { Component, useState, useEffect } from 'react'
import ReactDOM from 'react-dom'
import './style.css';
function gc() {
  var str = "#";
  var c = "0123456789ABCDEF";
  for (var i = 0; i < 6; i++) {
    str += c[Math.floor(Math.random() * 15)]
  }
  return str
}
function gr(m) {
  let y = Math.floor(Math.random() * m.length);
  if (y > 0 && y < m) {
    y = Math.random() % 2 == 0 ? y + 1 : y - 1;
  }
  return y;
}
const Taker = ({ val, label, rf, min, max }) => {
  const [value, setValue] = useState(val);
  const em = { id: null };
  let eff = (v, c) => {
    if (rf != undefined) {
      rf({ [label]: v, case: c });
    }
  }
  let di = () => {
    if (min && value <= min) {
      return;
    }
    setValue(value - 1);
    eff(value - 1, 'd')
  }
  let hi = () => {
    if (max && value >= max) {
      return;
    }
    setValue(value + 1);
    eff(value + 1, 'i')
  }
  return <div className='containerTC'>
    <div>{label}</div>
    <div className='Taker'>
      <div onClick={di} className='sw dec'  >-</div>
      <div className='sw val'> {value}</div>
      <div className='sw inc' onClick={hi}  >+</div>
    </div>
  </div>;
}
export class Www extends React.Component {
  state = {
    players: { a: 0, b: 0, c: 0, d: 0 }, count: 0, counts: 10
  }
  componentDidMount() {
    this.handlePlayers({ player: 1, case: 'i' });
    document.documentElement.style.setProperty('--bgg', gc());
  }
  play() {
    var d = setInterval(this.start, 1000);
    this.setState({ id: d });
  }
  componentDidUpdate() {
    if (this.state.counts === this.state.count) {
      clearInterval(this.state.id)
      return;
    }
  }
  start = () => {
    {

      let k = Object.keys(this.state.players);
      let i = gr(k)
      let l = k[i]
      this.setState({ players: { ...this.state.players, [l]: this.state.players[l] + 1 }, turns: l, count: this.state.count + 1 });
    }
  }
  shouldComponentUpdate(x, y) {
    return true;
  }
  handle = (g) => {
    this.setState(g)
  }
  handlePlayers = (g) => {
    let pc = String.fromCharCode(96 + g.player);
    let letters = /[a-z]+/;
    if (!pc.match(letters)) return
    let keys = Object.keys(this.state.players)
    let i = keys.findIndex(h => h === pc);
    let newplayers = this.state.players;
    if (g.case === 'i') {
      newplayers = { ...newplayers, [pc]: 0 };
    } else {
      delete newplayers[keys[keys.length - 1]];
    }
    this.setState({ players: newplayers })
  }
  render() {
    return <div className='cen'>
      <div style={{ width: '100%' }} >
        <div className='headsup'>
          Scores</div>
        <div className='controlls'>
          <div className='fw'>
            <Taker val={4} label='player' rf={this.handlePlayers} max='26' min='1' />
            <Taker val={this.state.counts} label='counts' rf={this.handle} max='26' min='5' />
            <Taker val={null} label='Bet' rf={this.handle} />
          </div>
          <div className='containerTC play' onClick={this.play.bind(this)} >Play {this.state.counts - this.state.count}</div>
          <div style={{ width: '50%' }}>
            <ul className='horz'>
              {Object.keys(this.state.players).map((e, i) => <li key={i} className={'xo ' + (this.state.turns === e ? 'www' : 'xxx')} >{e} :{this.state.players[e]}</li>)}
            </ul>
          </div>
        </div>

      </div></div>;
  }
}
let e = <>
  <Www></Www>

</>
ReactDOM.render(
  e,
  document.getElementById('root')
);
